@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:"Nunito", sans-serif;padding:0;margin:0;color:#505050}html{scroll-behavior:smooth}.container{display:flex;flex-direction:column;justify-content:center;align-items:center;width:100vw}

.form__location{margin:1rem}.form__location input{width:60vh;font-size:1rem;padding:0.5rem;outline:none;border:2px solid #fff;box-shadow:2px 2px 4px rgba(0,0,0,0.2),-2px -2px 4px rgba(0,0,0,0.1)}@media (min-width: 768px) and (max-width: 1023px){.form__location input{width:70vw}}@media (max-width: 768px){.form__location input{width:90vw}}

.weather__content{display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:1rem;justify-content:center;width:100%}@media (min-width: 768px) and (max-width: 1023px){.weather__content{grid-template-columns:1fr 1fr}}@media (max-width: 768px){.weather__content{grid-template-columns:1fr}}.weather__content .weather__item{display:flex;flex-direction:column;box-shadow:2px 2px 4px rgba(0,0,0,0.2),-2px -2px 4px rgba(0,0,0,0.1);padding:1rem}.weather__content .weather__item .date{text-align:center;border-bottom:1px solid cyan}.weather__content .weather__item img{margin-right:0.5rem}.weather__content .weather__item .row{display:grid;grid-template-columns:1fr 1fr;margin:0.5rem}.weather__content .weather__item .row .col{display:flex;flex-direction:row;align-items:center}

